/**
 * Edit type: Select2 - Dropdown
 *
 * @since 1.0
 */
jQuery.fn.cacie_edit_select2_dropdown = function( column, item ) {
	let $ = jQuery;
	let column_editable = item.columndata[ column.column_name ];
	let options = column.editable.options;
	let el = $( this );

	// populate options from object
	let defaults = {
		type : 'ac_select2',
		showbuttons : true,
		source : cacie_options_format_editable( options ),
		select2 : {
			width : '230px'
		}
	};

	// populate options by ajax
	if ( column.editable.ajax_populate ) {
		let _args = {
			editable : column_editable,
			source : '',
			showbuttons : false,
			select2 : {
				width : 230,
				minimumInputLength : 0,
				escapeMarkup : function( text ) { return $( '<div>' + text + '</div>' ).text(); },
				ajax : {
					url : ajaxurl,
					dataType : 'json',
					quietMillis : 100,
					data : function( params ) {
						return {
							action : 'acp_editing_get_options',
							layout : AC.layout,
							searchterm : params.term,
							page : params.page,
							column : column.column_name,
							list_screen : AC.list_screen,
							item_id : item.ID,
							_ajax_nonce : AC.ajax_nonce
						};
					},
					processResults : function( response ) {
						if ( response.success ) {
							return response.data;
						}

						// Close Select2 dropdown
						el.data( 'editable' ).input.$input.find( 'select' ).select2( 'close' );

						// Output error
						el.data( 'editable' ).container.$form.editableform( 'error', response.data );

						return { results : [] };
					}
				}
			}
		};

		defaults = $.extend( defaults, _args );
	}

	if ( typeof column.editable.multiple !== 'undefined' && column.editable.multiple ) {
		defaults.select2.multiple = true;
		defaults.showbuttons = true;
	}

	$( this ).cacie_xeditable( defaults, column, item );

	if ( typeof column.editable.ajax_populate !== 'undefined' && column.editable.ajax_populate ) {
		$( this ).on( 'shown', function() {

			let inp = el.data( 'editable' ).input.$input.find( 'select' );

			inp.on( 'change', function() {
				column_editable.formattedvalue = [];
				let currentdata = inp.select2( 'data' );

				if ( typeof currentdata.id !== 'undefined' ) {
					column_editable.formattedvalue[ currentdata.id ] = currentdata.text;
				}
				else {
					for ( let i in currentdata ) {
						if ( currentdata.hasOwnProperty( i ) ) {
							column_editable.formattedvalue[ currentdata[ i ].id ] = currentdata[ i ].text;
						}
					}
				}
			} );

		} );
	}
};

// xEditable AC Select2
(function( $ ) {
	"use strict";

	let ACP_Select2_Input = function( options ) {
		this.init( 'ac_select2', options, ACP_Select2_Input.defaults );
	};

	$.fn.editableutils.inherit( ACP_Select2_Input, $.fn.editabletypes.abstractinput );

	$.extend( ACP_Select2_Input.prototype, {

		render : function() {
			let $container = this.$input;
			let $select = $container.find( 'select' );
			let options = this.options.source;
			let formatted_values = this.options.editable.formattedvalue;

			if ( formatted_values ) {
				Object.keys( formatted_values ).forEach( function( key ) {
					let text = formatted_values[ key ];
					$select.append( `<option value="${key}">${text}</option>` );
				} );
			}

			if ( options ) {
				options.forEach( ( option ) => {
					$select.append( `<option value="${option.value}">${option.text}</option>` );
				} );
			}

			if ( this.options.select2.multiple ) {
				$container.find( 'select' ).prop( 'multiple', true );
				$container.find( 'select option' ).prop( 'selected', false );
			}

			if ( !this.options.showbuttons ) {
				$select.on( 'change', function() {
					$container.parents( 'form:first' ).submit();
				} );
			}
		},

		value2input : function( value ) {
			let s2_options = this.options.select2;
			let $container = this.$input;
			s2_options.theme = 'acs2';

			if ( typeof value === "object" ) {
				value.forEach( function( v ) {
					$container.find( 'select' ).find( `option[value="${v}"]` ).prop( 'selected', true );
				} );
			}

			if ( typeof value === "string" ) {
				$container.find( 'select' ).find( `option[value="${value}"]` ).prop( 'selected', true )
			}

			$container.find( 'select' ).ac_select2( s2_options );
		},

		input2value : function() {
			return this.$input.find( "[name='ac_input_text']" ).val();
		}

	} );

	let template = `
		<div class="single-input">
			<select type="text" name="ac_input_text"></select>
		</div>
	`;

	ACP_Select2_Input.defaults = $.extend( {}, $.fn.editabletypes.abstractinput.defaults, {
		tpl : template,
		select2 : {},
		source : {},
		editable : {}
	} );

	$.fn.editabletypes.ac_select2 = ACP_Select2_Input;
}( window.jQuery ));