// xEditable DateTime

(function( $ ) {
	"use strict";

	let EditableDateTime = function( options ) {
		this.init( 'date_time', options, EditableDateTime.defaults );
	};

	$.fn.editableutils.inherit( EditableDateTime, $.fn.editabletypes.abstractinput );

	$.extend( EditableDateTime.prototype, {

		render : function() {
			let $container = this.$input;
			let $date = $container.find( 'input[name=date]' );
			let $time_hours_field = $container.find( 'select[name=time_hours]' );
			let $time_minutes_field = $container.find( 'select[name=time_minutes]' );
			let $time_seconds_field = $container.find( 'select[name=time_seconds]' );

			jQuery( 'body' ).addClass( 'ac-jqui' );

			$date.datepicker( {
				dateFormat : 'yy-mm-dd',
				changeYear : true,
				firstDay : this.options.weekstart,
				onSelect : function() {
					$time_hours_field.focus();
				}
			} );

			for ( let i = 0; i < 24; i++ ) {
				let $option = $( '<option>' );
				let val = ('0' + i).slice( -2 );
				let label = (12 === this.options.timeformat) ? this.formatAMPM( i ) : val;

				$option.val( val ).text( label );
				$time_hours_field.append( $option );
			}
			$time_hours_field.find( 'option:first' ).prop( 'selected', true );

			for ( let i = 0; i < 60; i++ ) {
				let $option = $( '<option>' );
				let val = ('0' + i).slice( -2 );

				$option.val( val ).text( val );
				$time_minutes_field.append( $option.clone() );
				$time_seconds_field.append( $option );
			}
			$time_minutes_field.val( '00' );
			$time_seconds_field.val( '00' );

		},

		formatAMPM : function( hours ) {
			let ampm = hours >= 12 ? 'pm' : 'am';
			hours = hours % 12;
			hours = hours ? hours : 12; // the hour '0' should be '12'

			return hours + ' ' + ampm;
		},

		value2input : function( value ) {
			if ( !value ) {
				return;
			}
			let $container = this.$input;
			let $date = $container.find( 'input[name=date]' );
			let $time_hours_field = $container.find( 'select[name=time_hours]' );
			let date = value.substr( 0, 10 );
			let hours = value.substr( 11, 2 );
			let minutes = value.substr( 14, 2 );
			let seconds = value.substr( 17, 2 );

			$date.val( date ).datepicker( 'refresh' );
			$time_hours_field.val( hours );
			$container.find( 'select[name=time_minutes]' ).val( minutes );
			$container.find( 'select[name=time_secondshours]' ).val( seconds );
		},

		input2value : function() {
			jQuery( 'body' ).removeClass( 'ac-jqui' );

			let date = $( '#frm_date' ).val();
			let hours = $( '#frm_time_hours' ).val();
			let minutes = $( '#frm_time_minutes' ).val();
			let seconds = $( '#frm_time_seconds' ).val();

			if ( !date || !hours || !minutes || !seconds ) {
				return false;
			}

			return date + ' ' + hours + ':' + minutes + ':' + seconds;
		}
	} );

	let template = `
	<div class="acf-editable-date-time">
		<div class="acf-editable-date-time__date">
			<label>${ACP_Editing.i18n.date}</label>
			<input type="text" id="frm_date" name="date" placeholder="yyyy-mm-dd" required class="form-control" autocomplete="off">
		</div>
		<div class="acf-editable-date-time__time">
			<label>${ACP_Editing.i18n.time}</label>
			<select type="text" id="frm_time_hours" name="time_hours"></select>
			<select type="text" id="frm_time_minutes" name="time_minutes"></select>
			<select type="text" id="frm_time_seconds" name="time_seconds"></select>
		</div>
	</div>`;

	EditableDateTime.defaults = $.extend( {}, $.fn.editabletypes.abstractinput.defaults, {
		tpl : template,
		weekstart : 0,
		timeformat : 12
	} );

	$.fn.editabletypes.date_time = EditableDateTime;

}( window.jQuery ));

jQuery.fn.cacie_edit_date_time = function( column, item ) {

	let el = jQuery( this );

	el.cacie_xeditable( {
		type : 'date_time',
		value : el.cacie_get_value( column, item ),
		weekstart : column.editable.weekstart,
		timeformat : column.editable.timeformat
	}, column, item );

};