/**
 * Edit type: Select2 - Tags
 *
 * @since 1.0
 */
jQuery.fn.cacie_edit_select2_tags = function( column, item ) {
	let $ = jQuery;
	let el = $( this );
	let options = column.editable.options;
	let value = el.cacie_get_value( column, item );

	// e.g. no terms available
	if ( 'false' === value || false === value ) {
		value = '';
	}

	el.cacie_xeditable( {
		type : 'ac_select2_tags',
		value : value,
		source : cacie_options_format_editable( options ),
		select2 : {
			width : 200,
			tags : true,
			multiple : true,
			escapeMarkup : function( text ) { return $( '<div>' + text + '</div>' ).text(); }
		}
	}, column, item );
};

(function( $ ) {
	"use strict";

	let ACP_Select2_Tags = function( options ) {
		this.init( 'ac_select2_tags', options, ACP_Select2_Tags.defaults );
	};

	$.fn.editableutils.inherit( ACP_Select2_Tags, $.fn.editabletypes.abstractinput );

	$.extend( ACP_Select2_Tags.prototype, {

		render : function() {
			let $container = this.$input;
			let $select = $container.find( 'select' );
			let options = this.options.source;

			if ( options ) {
				options.forEach( ( option ) => {
					$select.append( `<option value="${option.value}">${option.text}</option>` );
				} );
			}

			$container.find( 'select' ).prop( 'multiple', true );
		},

		value2input : function( value ) {
			let s2_options = this.options.select2;
			let $container = this.$input;
			s2_options.theme = 'acs2';

			if ( typeof value === "object" ) {
				value.forEach( function( v ) {
					$container.find( 'select' ).find( `option[value="${v}"]` ).prop( 'selected', true );
				} );
			}

			$container.find( 'select' ).ac_select2( s2_options );
		},

		input2value : function() {
			return this.$input.find( "[name='ac_input_text']" ).val();
		}

	} );

	let template = `
		<div class="single-input">
			<select type="text" name="ac_input_text" multiple></select>
		</div>
	`;

	ACP_Select2_Tags.defaults = $.extend( {}, $.fn.editabletypes.abstractinput.defaults, {
		tpl : template,
		select2 : {},
		source : {},
		formatted_value : {}
	} );

	$.fn.editabletypes.ac_select2_tags = ACP_Select2_Tags;
}( window.jQuery ));
